@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  color: #222;
  font-size: 1.4em;
  line-height: 1;
  height: 100%;
  background: url(https://raw.githubusercontent.com/stefanhk31/princess-bride-quotes/gh-pages/src/princess-bride-background-2_result.jpg) no-repeat center center;
  background-size: cover;
  overflow: hidden; }

body {
  position: relative;
  z-index: 1;
  font-family: sans-serif;
  font-size: 1.4em;
  font-weight: 400;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px;
  padding: 0px; }

.footer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  display: block;
  margin: 15px auto 30px auto;
  font-size: .8em;
  color: white;
  border-radius: 5px;
  padding: .2em;
  background-color: #333; }
  .footer a {
    font-weight: 500;
    text-decoration: none;
    color: white; }

#quote-box {
  border-radius: 15px;
  border-style: solid;
  position: relative;
  z-index: 2;
  margin: auto;
  width: 40vw;
  padding: 40px 50px;
  display: table;
  background-color: rgba(255, 255, 255, 0.5); }
  #quote-box .quote-text {
    text-align: center;
    width: 450px;
    height: auto;
    clear: both;
    font-weight: 500;
    font-size: 1.6em; }
    #quote-box .quote-text i {
      font-size: 1.0em;
      margin-right: 0.4em; }
  #quote-box .quote-author {
    width: 450px;
    height: auto;
    clear: both;
    padding-top: 20px;
    font-size: 1em;
    text-align: right; }
  #quote-box .buttons {
    width: auto;
    margin: auto;
    display: flex;
    justify-content: space-between; }
    #quote-box .buttons .button {
      height: auto;
      border: none;
      border-radius: 10px;
      color: #fff;
      background-color: #333;
      outline: none;
      font-size: 0.85em;
      padding: 8px 18px 6px 18px;
      margin-top: 30px;
      opacity: 1;
      cursor: pointer; }
      #quote-box .buttons .button:hover {
        opacity: 0.9; }
      #quote-box .buttons .button#tweet-quote, #quote-box .buttons .button#tumblr-quote {
        float: left;
        padding: 6px;
        text-align: center;
        font-size: 1.2em;
        height: auto;
        width: auto; }
      #quote-box .buttons .button#new-quote {
        float: right; }

